.about{
    padding-top: 39px;
    padding-bottom: 50px;
   
     @media screen and (min-width: 768px) {
      padding: 50px 0;
    }
    
}

.about-title{
padding: 0;
margin: 0;
margin-bottom: 20px;

font-family: $main-font;
font-style: normal;
font-weight: 800;
font-size: 20px;
line-height: 1.3;

letter-spacing: 0.03em;

color: #111518;

@media screen and (min-width: 768px) {
      font-size: 26px;
      margin-bottom: 30px;
    }

@media screen and (min-width: 1314px) {
      font-size: 46px;
      margin-bottom: 50px;
  
    }

}


.about-position{
    display: flex;
    flex-wrap: wrap;

    @media screen and (min-width: 768px) {
     flex-wrap: nowrap;
  
    }
     @media screen and (min-width: 1314px) {
      max-width: 1280px;
    }
    
}

.about-position__bloc{
    @media screen and (min-width: 768px) {
            &:not(:last-child){
            min-width: 243px; 
            margin-right: 44px; 
            }
        }

        @media screen and (min-width:1314px) {
            &:not(:last-child){
            min-width: 530px; 
            margin-right: 30px; 
            }
        }
}




.about-title__name{
font-family: $main-font;
font-style: normal;
font-weight: 800;
font-size: 20px;
line-height: 1.2;
letter-spacing: 0.08em;

color: #111518;

position: relative;

padding: 0;
margin: 0;
margin-top: 20px;
margin-bottom: 30px;
    
    &::after{

        content: "";
        display: block;
        position: absolute;
        width: 100%;
        height: 1px;
        border: 1px solid #B9B9B9; 
        margin-top: 10px;

        @media screen and (min-width: 1314px) {
        margin-top: 15px;
         }
    }

    @media screen and (min-width: 768px) {
        margin-top: 0;
    }

    @media screen and (min-width: 1314px) {
        margin-bottom: 45px;
        font-size: 30px;
    }
}


.about-list{
    margin: 0;
    padding: 0;

    list-style: none;

    height: 179px;
    display: flex;
    flex-wrap: wrap;
    align-content: space-around;

    @media screen and (min-width: 768px) {
        height: 165px;
        
        }
    @media screen and (min-width: 1314px) {
      max-width: 600px;
    }
}

.about-item{
    display: flex;
    flex-wrap: nowrap;

    align-items: center;

    margin-left: calc(-1 * 9px);

     @media screen and (min-width: 1314px) {
     margin-left: calc(-1 * 34px);
    }
   

}
.about-item__position{
    margin-left: 9px;
     
    @media screen and (min-width: 1314px) {
     margin-left: 34px;
  
    }
     
}

.about-item__img{
    padding: 0;
    margin: 0;
    width: 17px;
    height: 14px;

   @media screen and (min-width: 1314px) {
     width: 24.4px;
    height: 20px;
  
    }
}

.about-item__sub{
    padding: 0;
    margin: 0;

    
    font-style: normal;
    font-weight: 600;
    font-size: 14px;
    line-height: 1.21;
    letter-spacing: 0.06em;

    color: #111518;

    @media screen and (min-width: 768px) {
        font-size: 12px;
        line-height: 1.4;
        }

    @media screen and (min-width: 1314px) {
        font-size: 16px;
        }
}

.about-list__border{
    padding: 0;
    margin: 0;


    font-style: normal;
    font-weight: bold;
    font-size: 20px;
    line-height: 1.65;

    letter-spacing: 0.08em;

    color: #FF6B09;

    @media screen and (min-width: 1314px) {
        margin:15px 0;
        }
}

.about-description{
    padding: 0;
    margin-bottom: 10px;
    
    font-weight: 500;
    font-size: 12px;
    line-height: 1.67;

    color: #B9B9B9;

        @media screen and (min-width: 768px) {
            margin-bottom: 24px;
            font-size: 10px;
            line-height: 2;
        }

        @media screen and (min-width: 1314px) {
        font-size: 18px;
        line-height: 1.56;
        min-height: 310px ;
        margin-bottom: 20px;
        }

         
}

.about-description__position{
    display: none;

    @media screen and (min-width: 1314px) {
     display:initial;
        }
}


.about-link{
    padding: 0;
    font-weight: 500;
    font-size: 12px;
    line-height: 1.67;

    text-decoration-line: underline;

    color: #FF6B09;

    @media screen and (min-width: 1314px) {
        font-size: 18px;
        line-height: 1.56;
        }
}


